.menu { 
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
  width: 50%; 
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  background-color: #111518;
  overflow-x: hidden;
  transition: 0.3s;  
  padding-right: 68px;  
}

.menu_hidden .menu__list {
  display: none;
}

.menu_hidden {
  transition: 0.3s;
  width: 0;
  z-index: -1;
}

.transparent-menu {
  cursor: pointer;
  display: flex;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; 
  background-color: rgba(21, 27, 31, 0.81);
  margin-left: 68px;  
  transition: 0.4s;
  z-index: 1;
}

.transparent-menu_hidden {
  transition: 0.4s;
  width: 0; 
  z-index: 1;
}

.menu__list {
  list-style: none;
  margin-left: 128px; 
  margin-top: auto;
}

.menu__list ul {
  cursor: pointer;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 24px; 
  color: #FFFFFF; 
  padding-left: 0;
  animation: fadein 1s;
}

.menu__list ul:hover {
  color: #0ec261; 
}

.menu__social-icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-left: 128px;
  margin-bottom: auto;
}

.menu__social-icon {
  cursor: pointer;
  margin-right: 24px; 
  fill: white;
} 

.menu__social-icon:hover { 
  fill: #0ec261;
}

@media (max-width: 1220px) {
  .transparent-menu {
    width: 0%;
  }
  .menu {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .transparent-menu {
    width: 0%;
  }
  .menu {
    width: 100%;
  }
  .menu__list { 
    margin-left: 24px; 
    margin-top: 80px;
  }
  .menu__social-icons {
    margin-left: 24px; 
  }
}  

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}